* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.btn-container {
  justify-content: center;
  gap: 10px;
  display: flex;
}

.button-64 {
  box-sizing: border-box;
  color: #fff;
  max-width: 100%;
  min-width: 140px;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
  cursor: pointer;
  background-image: linear-gradient(144deg, #af40ff, #5b42f3 50%, #00ddeb);
  border: 0;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  padding: 3px;
  font-family: Phantomsans, sans-serif;
  font-size: 20px;
  line-height: 1em;
  text-decoration: none;
  display: flex;
  box-shadow: 0 15px 30px -5px rgba(151, 65, 252, .2);
}

.button-64:active, .button-64:hover {
  outline: 0;
}

.button-64 span {
  width: 100%;
  height: 100%;
  background-color: #05062d;
  border-radius: 6px;
  padding: 16px 24px;
  transition: all .3s;
}

.button-64:hover span {
  background: none;
}

@media (min-width: 768px) {
  .button-64 {
    min-width: 196px;
    font-size: 24px;
  }
}

.button-64[disabled] {
  color: gray;
  box-shadow: none;
  cursor: not-allowed;
  background-image: none;
}

.button-64[disabled] span {
  background-color: #d3d3d3;
}

